* {
  font-family: Helvetica, Arial, sans-serif;
  margin: 0px;
}

html,
body {
  -ms-overflow-style: none;
  scrollbar-width: none;
  height: 100vh;
  width: 100vw;
  background-color: black !important;
}
body::-webkit-scrollbar{
  display: none;
}
.btn-hover:hover{
  background-image: linear-gradient(rgba(0, 0, 0, 0.4) 0 0) !important;
}
hr{
  opacity: .75 !important;
  border: 0;
  width: 100%;               
  height: 2px !important;
}
/* .boxphoto:hover{
  box-shadow: inset  0 -70px 90px -10px black;
} */
/* .boxphoto{
  box-shadow: inset  0 -10px 50px -10px black;
} */
.box:hover{
  box-shadow: inset  0 -200px 150px -10px black;
}
.box{
  box-shadow: inset  0 -80px 130px -10px black;
}
.body__loading {
  background-color: #000000;
  display: grid;
  place-content: center;
  min-height: 100vh;
  margin: 0;
  padding: 2px;
  box-sizing: border-box;
  overflow: hidden;
}

h1 {
  color: white;
  font-family: "Open Sans", -apple-system, "Segoe UI", sans-serif;
  font-size: 50px;
  font-weight: bold;
  box-sizing: border-box;
}

.dots {
  color: white;
  display: inline-flex;
}
.dots--animate .dot.z {
  -webkit-animation: scale 0.8s 0.2s forwards;
          animation: scale 0.8s 0.2s forwards;
}
.dots--animate .dot.f, .dots--animate .dot.s {
  -webkit-animation: right 0.5s forwards;
          animation: right 0.5s forwards;
}
.dots--animate .dot.l {
  -webkit-animation: rightDown 0.4s 0.1s forwards linear, drop 2s 0.4s forwards linear;
          animation: rightDown 0.4s 0.1s forwards linear, drop 2s 0.4s forwards linear;
}

.dot {
  display: inline-block;
  width: 10px;
  height: 10px;
  background: white;
  border-radius: 10px;
  position: relative;
  margin-left: 6px;
}
.dot.z {
  position: absolute;
  -webkit-transform: scale(0);
          transform: scale(0);
}
@-webkit-keyframes scale {
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
@keyframes scale {
  100% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}
.dot.f, .dot.s {
  -webkit-transform: translateX(0px);
          transform: translateX(0px);
}
@-webkit-keyframes right {
  100% {
    -webkit-transform: translateX(16px);
            transform: translateX(16px);
  }
}
@keyframes right {
  100% {
    -webkit-transform: translateX(16px);
            transform: translateX(16px);
  }
}
.dot.t {
  background: transparent;
}
.dot .l {
  margin-left: 0;
  position: absolute;
  top: 0;
  left: 0;
}
@-webkit-keyframes rightDown {
  50% {
    top: 4px;
    left: 16px;
  }
  100% {
    top: 12px;
    left: 24px;
  }
}
@keyframes rightDown {
  50% {
    top: 4px;
    left: 16px;
  }
  100% {
    top: 12px;
    left: 24px;
  }
}
@-webkit-keyframes drop {
  100% {
    -webkit-transform: translate(70px, calc(35px + 50vh));
            transform: translate(70px, calc(35px + 50vh));
  }
}
@keyframes drop {
  100% {
    -webkit-transform: translate(70px, calc(35px + 50vh));
            transform: translate(70px, calc(35px + 50vh));
  }
}

/* modal */
.ReactModalPortal > div {
  opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  transition: opacity 0.2s ease-in-out;
  z-index: 999;
}

.modal-fondo {
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0;
}

.modal {
  background: white;
  border-radius: 5px;
  color: #333333;
  display: inline;
  max-height: 620px;
  max-width: 500px;
  outline: none;
  padding: 10px;
}
.dropdown-item-default:hover{
  color: black !important;
  background-color: white !important;
}
.dropdown-item-danger:hover{
  color: white !important;
  background-color: red !important;
}
