@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@800&display=swap");*{font-family:Helvetica, Arial, sans-serif;margin:0px}html,body{height:100vh;width:100vw}main{width:100%}::-webkit-scrollbar{width:10px}::-webkit-scrollbar-track{background:#f1f1f1}::-webkit-scrollbar-thumb{background:#888}::-webkit-scrollbar-thumb:hover{background:#555}.body__loading{background-color:#000;display:grid;place-content:center;min-height:100vh;margin:0;padding:2px;box-sizing:border-box;overflow:hidden}h1{color:white;font-family:"Open Sans", -apple-system, "Segoe UI", sans-serif;font-size:50px;font-weight:bold;box-sizing:border-box}.dots{color:white;display:inline-flex}.dots--animate .dot.z{animation:scale 0.8s 0.2s forwards}.dots--animate .dot.f,.dots--animate .dot.s{animation:right 0.5s forwards}.dots--animate .dot.l{animation:rightDown 0.4s 0.1s forwards linear, drop 2s 0.4s forwards linear}.dot{display:inline-block;width:10px;height:10px;background:white;border-radius:10px;position:relative;margin-left:6px}.dot.z{position:absolute;transform:scale(0)}@keyframes scale{100%{transform:scale(1)}}.dot.f,.dot.s{transform:translateX(0px)}@keyframes right{100%{transform:translateX(16px)}}.dot.t{background:transparent}.dot .l{margin-left:0;position:absolute;top:0;left:0}@keyframes rightDown{50%{top:4px;left:16px}100%{top:12px;left:24px}}@keyframes drop{100%{transform:translate(70px, calc(35px + (100vh / 2)))}}.ReactModalPortal>div{opacity:0}.ReactModalPortal .ReactModal__Overlay{align-items:center;display:flex;justify-content:center;transition:opacity 0.2s ease-in-out;z-index:999}.modal-fondo{background-color:rgba(0,0,0,0.3);bottom:0;left:0;right:0;top:0;position:fixed}.ReactModalPortal .ReactModal__Overlay--after-open{opacity:1}.ReactModalPortal .ReactModal__Overlay--before-close{opacity:0}.modal{background:white;border-radius:5px;color:#333;display:inline;max-height:620px;max-width:500px;outline:none;padding:10px}
