.login-container{
    padding:1%;
}
.login-form-1{
    border-radius: 5px;
    border: 2px solid #000;
    box-shadow: 0 0 8px 0 rgba(240, 240, 240, 1), 0 0 26px 0 rgba(240, 240, 240, 1);
    padding: 2%;
    margin-left:4%;
    margin-top:20%;
}
.login-form-3{
    border-radius: 5px;
    border: 2px solid #000;
    /*box-shadow:  offset-x | offset-y | blur-radius | spread-radius | color */
    box-shadow: 0 0 8px 0 rgba(240, 240, 240, 1), 0 0 26px 0 rgba(240, 240, 240, 1);
    padding: 2%;
    margin-left:25%;
}
.login-form-1 h3{
    text-align: center;
    color: white;
}
.login-form-3 h3{
    text-align: center;
    color: white;
}
.login-form-2{
    border-radius: 5px;
    box-shadow: 0 5px 8px 0 rgba(0, 0, 0, 0.2), 0 9px 26px 0 rgba(0, 0, 0, 0.19);
    padding: 4%;
    background: white;
}
.login-form-2 h3{
    text-align: center;
    color: white;
}
.login-container form{
    padding: 10%;
}
.btnSubmit
{
    width: 50%;
    border-radius: 1rem;
    padding: 1.5%;
    border: none;
    cursor: pointer;
}
.login-form-1 .btnSubmit{
    color: white;
    background-color: #000;
}
.login-form-3 .btnSubmit{
    color: white;
    background-color: #000;
}
.login-form-2 .btnSubmit{
    background-color: white;
    color: #000;
}
.login-form-2 .ForgetPwd{
    color: white;
    font-weight: 600;
    text-decoration: none;
}
.login-form-1 .ForgetPwd{
    color: white;
    font-weight: 600;
    text-decoration: none;
}
.login-form-3 .ForgetPwd{
    color: #000;
    font-weight: 600;
    text-decoration: none;
}
.form-control:focus {
    border-color: white !important;
    box-shadow: 0 0 5px 5px rgba(240, 240, 240, 0.61) !important;
}